.contenedor {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.tarjeta {
  margin: 5px;
  display: flex;
  width: 350px;
  height: 350px;
  background-color: yellow;
  flex-direction: column;
  align-items: center;
  border-radius: 25px;
}

.imagen {
  width: 225px;
  height: 225px;
  padding: 2px;
  border-radius: 13px;
}
.nombre {
  margin-top: 5px;
  text-align: center;
  color: black;
  display: flex;
  justify-content: space-around;
}
